<style lang="scss">
#asnForceWrap{
  width:100%;
}
#asnforce{
  background-color: rgba(10, 24, 43,0.9);
  width:100%;
  height:100%;
}
</style>
<template>
  <div id="asnForceWrap">
    <div id="asnForceChart">
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import http from '../../../../../assets/js/http';
  import echarts from 'echarts/lib/echarts';
  import {mapGetters} from 'vuex';
  export default {
    data: function (){
      return {
        asnChart: null,
        toolTips: null,
        nodes: '',
        links: ''
      }
    },
    watch: {
      keywords: function (val, oldval){
        this.$nextTick(function (){
          this.getNodes(val);
        });
      }
    },
    computed: {
      ...mapGetters({
        keywords: 'getAbilityQueryKeyword'
      })
    },
    methods: {
      asnOption (nodes, links) {
        return {
          title: {
            text: ''
          },
          tooltip: {
            "padding": 10,
            "formatter": function (param) {
              return `
                  <b style="color:#26b8f2">UPDATED</b> : ${typeof param.data.Updated === 'undefined' ? ' -' : param.data.Updated}</br>
                  <b style="color:#26b8f2">COUNTRY</b> : ${typeof param.data.Country === 'undefined' ? ' -' : param.data.Country}</br>
                  <b style="color:#26b8f2">INSTITUTE</b> : ${typeof param.data.institute === 'undefined' ? ' -' : param.data.institute}</br>
                  <b style="color:#26b8f2">DESC</b> : ${typeof param.data.Description === 'undefined' ? ' -' : param.data.Description}`
            }
          },
          animationDurationUpdate: 100,
          animationEasingUpdate: 'quinticInOut',
          series: [
            {
              type: 'graph',
              layout: 'force',
              roam: true,
              force: {
                repulsion: 300
              },
              label: {
                normal: {
                  show: true,
                  fontSize: 12,
                  position: 'right',
                  color: '#fff'
                }
              },
              lineStyle: {
                normal: {
                  type: 'solid',
                  color: '#fff',
                  width: 1,
                  curveness: 0.1
                }
              },
              itemStyle: {
                normal: {
                  color: '#26b8f2'
                }
              },
              nodes: nodes,
              links: links
            }
          ]
        }
      },
      getNodes (keywords) {
        // console.log(keywords)
        // console.log(this.$store.state.ability.abilityQueryKeyword)
        /*
        axios.all([
          axios.get('http://192.168.20.30:8000/bgpmap/vis/' + id + '/?token=2017&type=json'),
          axios.get('http://192.168.20.30:8000/bgpmap/asn/' + id + '/?token=2017')
        ]).then(axios.spread(function (vis, asn) {
          console.log(vis.data);
          console.log(asn.data);
        }))
        */
        let asnReg = /^\d+$/;
        let ipReg = /^(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9])\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9]|0)\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[1-9]|0)\.(25[0-5]|2[0-4][0-9]|[0-1]{1}[0-9]{2}|[1-9]{1}[0-9]{1}|[0-9])$/;
        let type = 'desc/' + keywords + '/?token=2017&type=aggre';
        if (asnReg.test(keywords)){
          type = 'vis/' + keywords + '/?token=2017&type=aggre'
        } else if (ipReg.test(keywords)){
          type = 'IP/' + keywords + '/?token=2017'
        }
        let visUrl = 'http://192.168.20.30:8000/bgpmap/' + type;
        this.$store.dispatch('load', true);// loading页面开始
        this.apiGet(visUrl).then(
          (res) => {
            this.$store.dispatch('load', false); // loading页面结束
            if (res.length > 500){
              this.$message({
                message: `已为您找到${res.length}个节点,但由于数据量过大,无法为您显示！`,
                type: 'warning'
              });
              return;
            }
            if (res.length === 0){
              this.$message({
                message: `没有更过节点了！`,
                type: 'warning'
              });
              return;
            }
            this.nodes = res.data.nodes;
            this.links = res.data.links;
            this.asnChart.setOption(this.asnOption(this.nodes, this.links));
          },
          (err) => {
            this.$store.dispatch('load', false);// loading页面结束
            this.$message(err)
          }
        )
      }
    },
    updated () {
    },
    mounted () {
      // 动态设置地图高度
      _g.setChartHeight('asnforce', 'asnForceChart');
      let asnForceChart = document.getElementById('asnForceChart');
      this.asnChart = echarts.init(asnForceChart);
      window.addEventListener("resize", function () {
        self.asnChart.resize();
      });
      let self = this;
      this.asnChart.on('click', function (param){
        self.getNodes(param.data.id);
      })
      this.getNodes(this.keywords);
    },
    mixins: [http]
  }
</script>



